function input(props){

    return (
        <div>
            <textarea
                id='editor'
                value={props.input}
                onChange={props.change}
            ># Why React? \r
            ## I certainly didn\'t need if for my 2-component 1-page program! \r
            ### I like React and I wanted to use it obviously! \r
            \r of *madness* **is** this \r what \r ___</textarea>
            <style jsx>{`
                #editor {
                    width: 800px;
                    height: 200px;
                    margin: auto;
                } 
            `}</style>
        </div>
    )
}

export default input;